@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@300;400;500;600;700;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  font-family: 'Rubik', sans-serif;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.cardUserInfo{margin:20px;margin-bottom:50px}.cardUserInfo__avatar{width:150px;border-radius:50%}

.navBar{height:60px;width:100%;background:linear-gradient(90deg, #2c2d2e 4%, #000b0d 100%);display:flex;justify-content:center;align-items:center}.navBar__title{color:#fff;font-size:30px}

.btnLink{border:none;background:linear-gradient(90deg, #2c2d2e 4%, #000b0d 100%);color:#fff;text-transform:uppercase;padding:10px 25px;border-radius:10px;font-weight:800;transition:all .6s ease-in-out;cursor:pointer}.btnLink:hover{background:#fff;color:#000;border:1px solid black}

.project{max-width:300px;height:200px;box-shadow:0px 1px 5px 1px #8a8a8a;border-radius:10px;margin:10px;padding:14px;display:flex;flex-direction:column;justify-content:space-between;grid-gap:18px;gap:18px;transition:all .4s}.project__title{font-size:25px;font-weight:500;margin:5px}.project:hover{transform:scale(1.05)}.project__buttons{display:flex;grid-gap:10px;gap:10px;justify-content:center}

.projectsContainer__title{color:black;font-size:35px;margin:15px 0}.projectsContainer__container{display:flex;width:100%;flex-direction:row;justify-content:center;align-items:center;flex-wrap:wrap}

